import React, { memo, useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { MpSdk, setupSdk } from '@matterport/sdk';
import { MatterportContainerWrapper } from './styled';
import { selectDetailProject } from '../selectors';

export const MatterportContainer = memo(() => {
  const containerRef = useRef<HTMLIFrameElement>(null!);

  const detailProject = useSelector(selectDetailProject);

  const [, setSdk] = useState<MpSdk>(null!);

  useEffect(() => {
    if (
      containerRef.current &&
      detailProject?.matterport_key &&
      detailProject?.matterport_space
    ) {
      setupSdk(detailProject?.matterport_key, {
        container: containerRef.current,
        space: detailProject?.matterport_space,
        iframeQueryParams: detailProject.matterport_config?.reduce(
          (result, item) => {
            result[item.key] = item.value;
            return result;
          },
          {},
        ),
      }).then(setSdk);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [detailProject?.matterport_key, detailProject?.matterport_space]);

  return <MatterportContainerWrapper ref={containerRef} />;
});

export default MatterportContainer;
